.container {
  padding-inline: 30px;
  margin-inline: auto;
  max-width: 320px;
}
@media (min-width: 768px) {
  .container {
    padding-inline: 34px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding-inline: 123px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 1440px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Inter, sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 52px;
  text-transform: uppercase;
  color: #333;
}
@media (min-width: 768px) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-size: 48px;
    line-height: 62px;
  }
}
@media (min-width: 1440px) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-size: 62px;
    line-height: 78px;
  }
}

h2 {
  font-size: 30px;
  line-height: 40px;
}
@media (min-width: 768px) {
  h2 {
    font-size: 36px;
    line-height: 46px;
  }
}
@media (min-width: 1440px) {
  h2 {
    font-size: 56px;
    line-height: 70px;
  }
}

h3 {
  font-size: 18px;
  line-height: 22px;
}
@media (min-width: 768px) {
  h3 {
    font-size: 24px;
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  h3 {
    font-size: 34px;
    line-height: 42px;
  }
}

h4 {
  font-size: 22px;
  line-height: 28px;
}

h5 {
  font-size: 16px;
  line-height: 22px;
}

h6 {
  font-size: 14px;
  line-height: 20px;
}

.gap-page {
  display: flex;
  flex-direction: column;
  gap: 110px;
}
@media (min-width: 768px) {
  .gap-page {
    gap: 140px;
  }
}
@media (min-width: 1440px) {
  .gap-page {
    gap: 170px;
  }
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url('../fonts/Roboto-Regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.secondary-text {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: #7c7c7c;
}

.thirdary-text {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
}

.quote {
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
}
@media (min-width: 768px) {
  .quote {
    font-size: 24px;
    line-height: 36px;
  }
}

.button {
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  text-transform: uppercase;
  transition: 0.3s ease;
}
.button:hover {
  transform: scale(1.1);
}

* {
  box-sizing: border-box;
  transition: all 0.3s;
}

html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  text-size-adjust: none;
}

body {
  background-color: #f7f7f7;
}

footer,
header,
nav,
section,
main {
  display: block;
}

ol,
ul,
li {
  list-style: none;
}

button {
  all: unset;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  background-image: url('../images/header/img-mobile.svg');
  background-repeat: no-repeat;
  background-size: 320px 245px;
  background-position: center 20%;
}
@media (min-width: 768px) {
  .header {
    background-image: none;
  }
}
.header__content {
  padding-top: 285px;
}
@media (min-width: 768px) {
  .header__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    -moz-column-gap: 20px;
    column-gap: 20px;
    padding-top: 0;
    background-image: url('../images/header/img-tablet.svg');
    background-repeat: no-repeat;
    background-size: 430px 317px;
    background-position: right bottom;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .header__content {
    grid-template-columns: repeat(8, 1fr);
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .header__content {
    grid-template-columns: repeat(12, 1fr);
    -moz-column-gap: 30px;
    column-gap: 30px;
  }
}
@media (min-width: 1440px) {
  .header__content {
    background-image: url('../images/header/img-desktop.svg');
    background-size: 816px 464px;
    height: 464px;
  }
}
.header__title {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .header__title {
    grid-column: 1/5;
    margin-bottom: 30px;
  }
}
.header__text {
  color: #333;
}
@media (min-width: 768px) {
  .header__text {
    grid-column: 1/4;
  }
}

.top-bar {
