}
@media (min-width: 768px) {
  .top-bar {
    padding-top: 30px;
    margin-bottom: 130px;
  }
}
@media (min-width: 1440px) {
  .top-bar {
    margin-bottom: 90px;
  }
}
.top-bar__logo {
  cursor: pointer;
}
.top-bar__logo:hover {
