}
.benefits__nav-link:hover {
  font-weight: 700;
}
.benefits__cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  -moz-column-gap: 20px;
  column-gap: 20px;
}
@media (min-width: 768px) {
  .benefits__cards {
    grid-template-columns: repeat(8, 1fr);
  }
}
@media (min-width: 1440px) {
  .benefits__cards {
    grid-template-columns: repeat(12, 1fr);
    -moz-column-gap: 30px;
