.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0 0 100%;
  grid-column: 1/-1;
}
@media (min-width: 768px) {
  .card {
    grid-column: span 4;
    transition: transform 0.3s;
  }
  .card:hover {
    transform: scale(0.9);
  }
}
@media (min-width: 1440px) {
  .card {
    grid-column: span 3;
  }
}
.card__img {
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .card__img {
    margin-bottom: 50px;
  }
}
@media (min-width: 1440px) {
  .card__img {
