  }
}
.card__title {
  text-align: center;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .card__title {
    max-height: 28px;
    margin-bottom: 52px;
  }
}
.card__text {
  text-align: center;
}

.photo {
  display: flex;
  justify-content: center;
}
.photo__img {
  display: block;
