  }
}
@media (min-width: 1440px) {
  .about-us__container {
    padding-inline: 123px;
  }
}
@media (min-width: 768px) {
  .about-us__cards > :nth-child(1) {
    padding-right: 34px;
  }
  .about-us__cards > :nth-child(2) {
    padding-left: 34px;
  }
}
@media (min-width: 1440px) {
  .about-us__cards > :nth-child(1) {
    padding-right: 0;
  }
  .about-us__cards > :nth-child(2) {
