  }
}
@media (min-width: 768px) {
  .about-us__card {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    -moz-column-gap: 20px;
    column-gap: 20px;
    min-width: 0;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .about-us__card {
    grid-template-columns: repeat(8, 1fr);
