}
.about-us__title {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .about-us__title {
    grid-column: span 4;
    grid-row: 1;
  }
}
@media (min-width: 1440px) {
  .about-us__title {
    grid-column: span 6;
  }
}
.about-us__picture {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .about-us__picture {
    grid-column: 1/5;
    grid-row: 1/5;
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .about-us__picture {
    grid-column: 1/7;
    grid-row: 1/7;
    transition: transform 0.3s;
  }
  .about-us__picture:hover {
    transform: scale(1.05);
  }
}
@media (min-width: 768px) {
  .about-us__picture-right {
    grid-column: 5/-1;
  }
}
@media (min-width: 1440px) {
  .about-us__picture-right {
    grid-column: 7/-1;
  }
}
.about-us__img {
  display: block;
}
@media (min-width: 768px) {
  .about-us__img {
    width: 100%;
  }
}
.about-us__text {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .about-us__text {
    grid-column: span 4;
    grid-row: 2;
    margin-bottom: 60px;
  }
}
@media (min-width: 1440px) {
  .about-us__text {
    grid-column: span 6;
  }
}
.about-us__button {
  color: #0db2b3;
}
.about-us__button:hover {
  transform: none;
}
@media (min-width: 768px) {
  .about-us__button {
    grid-column: span 4;
    grid-row: 3;
  }
}
@media (min-width: 1440px) {
  .about-us__button {
    grid-column: span 6;
  }
}
.about-us__button-text {
  position: relative;
  display: inline-block;
}
.about-us__button-text::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 55px;
  height: 1px;
  background-color: #0db2b3;
  transition: width 0.3s ease;
}
@media (min-width: 1440px) {
  .about-us__button-text::after {
    width: 64px;
  }
}
.about-us__button-text:hover {
  transform: none;
}
.about-us__button-text:hover::after {
  width: 100%;
}

